@charset "UTF-8";
.file-row {
  display: flex;
}
.file-row a {
  background-color: white;
  text-decoration: none;
  color: #1c1c1c;
}
.file-row a:hover {
  text-decoration: none;
}
.file-row a:hover.box-shadow {
  box-shadow: 0 8px 13px 0 rgba(0, 0, 0, 0.36);
  transition: 150ms linear box-shadow;
}
.file-row a.box-shadow {
  transition: 150ms linear box-shadow;
}
.file-row .title {
  font-size: 18px;
  line-height: 24px;
  color: #0f2e68;
  font-weight: 700;
}
.file-row .tag {
  max-width: 205px;
}
.file-row .tag span:last-of-type {
  display: none;
}

.home-competencies .fake-h1 {
  color: #0f2e68;
}
.home-competencies h2 {
  color: #0f2e68;
}

.competencies-container {
  max-width: 1040px;
  margin: auto;
  gap: 20px;
}

.competency-block {
  text-decoration: none;
  color: #0f2e68;
  width: 100%;
  max-width: 245px;
  background: white;
  border-radius: 15px;
  transition: 150ms linear box-shadow;
}
.competency-block p {
  color: #0f2e68;
  font-weight: 600;
  font-size: 17px;
  line-height: 24px;
}
.competency-block:hover {
  box-shadow: 0 8px 13px 0 rgba(0, 0, 0, 0.16);
  transition: 150ms linear box-shadow;
  text-decoration: none;
}

@-webkit-keyframes bgGradient {
  0% {
    background-position: 0% 18%;
  }
  50% {
    background-position: 100% 83%;
  }
  100% {
    background-position: 0% 18%;
  }
}

@keyframes bgGradient {
  0% {
    background-position: 0% 18%;
  }
  50% {
    background-position: 100% 83%;
  }
  100% {
    background-position: 0% 18%;
  }
}
.footer {
  background: linear-gradient(141deg, #09214e, #2c487c);
  background-size: 200% 200%;
  -webkit-animation: bgGradient 10s ease infinite;
  animation: bgGradient 10s ease infinite;
}
.footer a,
.footer p {
  color: white;
}
.footer p {
  font-size: 14px;
  line-height: 24px;
}
.footer .contact-block {
  padding: 70px 20px;
}
.footer .contact-block p {
  font-weight: 600;
  font-size: 18px;
  line-height: 24px;
  color: white;
}
.footer h3 {
  font-size: 36px;
  line-height: 40px;
  color: white;
}

.footer-legal {
  border-top: 1px solid white;
  padding-top: 10px;
}

.footer-menu li.nav-item {
  display: flex;
  align-items: center;
}
.footer-menu li.nav-item:not(:last-of-type):after {
  margin: 0 3px;
  content: "â€¢";
  color: #ffa300;
}
.footer-menu li.nav-item a.nav-link {
  font-size: 14px;
  line-height: 24px;
  padding: 3px;
}
.footer-menu li.nav-item a.nav-link span {
  color: #ffa300;
  transition: 150ms linear all;
}
.footer-menu li.nav-item a.nav-link:hover span {
  color: #78c9e5;
  transition: 150ms linear all;
}

@media only screen and (max-width: 767px) {
  .footer-menu li.nav-item:after {
    display: none;
  }
}
body.video-open {
  overflow: hidden;
}
body.video-open header .header-inner .logo,
body.video-open header .header-inner .menu {
  opacity: 0;
  transition: 150ms linear all;
}
body.video-open header .header-inner:before {
  height: calc(100vh + 75px);
  transform: translateY(0);
}

.video-popup-container.visible {
  opacity: 1;
  pointer-events: auto;
  transition: 250ms linear all;
}
.video-popup-container.visible .video-popup {
  opacity: 1;
  transition: 150ms 250ms linear all;
}

button.close-btn, button.download-btn,
a.close-btn,
a.download-btn {
  color: white;
  opacity: 1;
  margin-bottom: 5px;
  border-radius: 0;
  font-weight: 100;
  border-width: 2px;
}
button.close-btn:hover, button.download-btn:hover,
a.close-btn:hover,
a.download-btn:hover {
  opacity: 1;
}

.video-popup video {
  max-width: 700px;
  height: auto;
  width: 100%;
}

button.download-btn,
a.download-btn {
  color: white;
}
button.download-btn span.download-text,
a.download-btn span.download-text {
  font-size: 18px;
  padding-right: 10px;
  overflow: hidden;
  color: white;
  line-height: 1;
}
button.download-btn .download-icon,
a.download-btn .download-icon {
  height: 27px;
  margin-bottom: 0px;
}
button.download-btn .download-icon svg,
a.download-btn .download-icon svg {
  fill: white;
}